@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
    display: flex;
    background: #1c1c1c;
    color: #f2f2f2;
    justify-content: space-between;
    border-bottom: 1px solid #2196F3;
    box-shadow: 4px 4px 10px -2px rgba(0,0,0,0.75);
}

nav h3 {
    margin-left: 10px;
    color: #2196F3;
    -webkit-transition: color 1s ease-out;
    transition: color 1s ease-out;
}

nav ul {
    margin-right: 10px;
    display: flex;
    list-style: none
}

nav ul li {
    font-weight: 800;
    padding: 5px;
}

nav ul li:hover {
    cursor: pointer;
    color: #2196F3;
    -webkit-transition: 0.4s ease-in;
    transition: 0.4s ease-in;
}
#search-con {
    margin-top: 5%;
    display: flex;
    justify-content: center;
}

.MuiAutocomplete-root {
    color: white
}

.card {
    border: 2px solid #2196F3;
    border-radius: 5px;
    box-shadow: 4px 4px 10px -2px rgba(0,0,0,0.75);
}

.date {
    margin-bottom: 30px;
}

.date h4 {
    margin: 0;
}
#daylist {
    margin: 10px;
    display: grid;
    grid-template-columns: repeat( auto-fill, minmax(180px, 1fr) );
    grid-gap: 10px;
}

#CurrentWeather-con {
    border: 2px solid #2196F3;
    border-radius: 5px;
    margin: 5%;
    padding: 3%;
}

#header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5%;
}



 #CurrentWeather-con h1 {
    margin: 0;
}

#CurrentWeather-con h4 {
    margin-top: 5px;
}

#five-day-con {
    margin: 5% 0
}

#favorites-button {
    border: 1px solid #f27c21;
    background-color: #f2f2f2;
    border-radius: 2px;
    height: 30px;
}

.star {
    opacity: 0.4;
    color: #ffe100;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.favorite {
    opacity: 1;
}

.star :hover {
    cursor: pointer;
}

#right-box-header{
    display: flex;
    flex-direction: column;
}

.toggle-unit{
    width: 30px;
    margin-top: 10px;
    border: 2px solid #2196F3;
    border-radius: 20px;
}

.toggle-unit {
    cursor: pointer;
}
#fav-card:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition : 0.5s;
    transition : 0.5s
}


#fav-card {
    -webkit-transition : 0.5s;
    transition : 0.5s
}
#favlist {
    margin: 10px;
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(180px, 250px) );
    justify-content: center;
    grid-gap: 20px;
}

#fav-con {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 30vh;
}
#five-day-con {
    margin-top: 0;
}
#footer {
    background: #1c1c1c;
    height: 150px;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

#footer h3 {
    font-weight: 500;
    color: #f2f2f2
}

#social a {
    text-decoration: none;
    color: #f2f2f2;
    margin: 10px;
}

#social a:hover {
    color: #2196F3;
    -webkit-transition: 0.3s ease-in;
    transition: 0.3s ease-in;
}
body {
position: relative;
  padding-bottom: 21.9rem;
  background-color: #f2f2f2;
  color: #333;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  font-family: 'Source Sans Pro', sans-serif;
}

